










import {Component, Prop, Vue} from 'vue-property-decorator';

@Component
export default class PutCenter extends Vue {
    @Prop({
        type: String,
        default: ""
    })
    private classes!: String;

    @Prop({
        type: String,
        default: ""
    })
    private styles!: String;

    @Prop({
        type: String,
        default: "1000px"
    })
    private max_width!: String;
}
